/****
 * MAIN NAVIGATION
 */

$(document).ready(function($)
{
  // Add class .active to current link - AJAX Mode off
  $.navigation.find('a').each(function()
  {
    var cUrl = String(window.location).split('?')[0];

    if (cUrl.substr(cUrl.length - 1) == '#')
    {
      cUrl = cUrl.slice(0,-1);
    }

    if ($($(this))[0].href==cUrl)
    {
      $(this).addClass('active');

      $(this).parents('ul').add(this).each(function()
      {
        $(this).parent().addClass('open');
      });
    }
  });

  // Dropdown Menu
  $.navigation.on('click', 'a', function(e)
  {
    if ($.ajaxLoad)
    {
      e.preventDefault();
    }

    if ($(this).hasClass('nav-dropdown-toggle'))
    {
      $(this).parent().toggleClass('open');
      resizeBroadcast();
    }
  });

  function resizeBroadcast()
  {

    var timesRun = 0;
    var interval = setInterval(function()
    {
      timesRun += 1;
      if(timesRun === 5)
      {
        clearInterval(interval);
      }
      window.dispatchEvent(new Event('resize'));
    }, 62.5);
  }

  /* ---------- Main Menu Open/Close, Min/Full ---------- */
  $('.navbar-toggler').click(function()
  {
    if ($(this).hasClass('sidebar-toggler'))
    {
      $('body').toggleClass('sidebar-hidden');
      resizeBroadcast();
    }

    if ($(this).hasClass('sidebar-minimizer'))
    {
      $('body').toggleClass('sidebar-minimized');
      resizeBroadcast();
    }

    if ($(this).hasClass('aside-menu-toggler'))
    {
      $('body').toggleClass('aside-menu-hidden');
      resizeBroadcast();
    }

    if ($(this).hasClass('mobile-sidebar-toggler'))
    {
      $('body').toggleClass('sidebar-mobile-show');
      resizeBroadcast();
    }

  });

  $('.sidebar-close').click(function()
  {
    $('body').toggleClass('sidebar-opened').parent().toggleClass('sidebar-opened');
  });

  /* ---------- Disable moving to top ---------- */
  $('a[href="#"][data-top!=true]').click(function(e)
  {
    e.preventDefault();
  });

  /****
   * INPUT MASK
   */

  /* ---------- Phone-Email ---------- */
  $('#phone').inputmask({"mask": "(+62) 999-9999-9999"});
  $('#email').inputmask(
  {
    mask: "*{1,20}[.*{1,20}][.*{1,20}][.*{1,20}]@*{1,20}[.*{2,6}][.*{1,2}]",
    greedy: false,
    onBeforePaste: function (pastedValue, opts)
    {
      pastedValue = pastedValue.toLowerCase();
      return pastedValue.replace("mailto:", "");
    },
    definitions:
    {
    '*':
      {
        validator: "[0-9A-Za-z!#$%&'*+/=?^_`{|}~\-]",
        cardinality: 1,
        casing: "lower"
      }
    }
  });

  /****
   * Json API
   */

  /* ---------- Json API- Login API ---------- */
  $.ajaxSetup(
  {
    headers:
    {
      'X-CSRF-TOKEN': $('meta[name="csrf-token"]').attr('content')
    }
  });

  $('#login-form').submit(function(event)
  {
    event.preventDefault();
    var postData =
    {
      'username': $('input[name=username]').val(),
      'password': $('input[name=password]').val(),
      'remember_me': $('input[name=remember_me]').is(':checked')
    };

    $.ajax(
      {
        type: 'POST',
        url: '/login',
        data: postData,
        dataType: 'JSON'
      })
      .done(function(response)
      {
        window.location.href = response.redirect;
      })
      .fail(function(response)
      {
        $('input[type=password]').val("");
        sOk('Kesalahan!', response.responseJSON.error, 'error');
      });
  });

  /* ---------- Json API - Register API ---------- */
  $('#fakultas_id').change(function (e)
  {
    var url = 'https://' + window.location.host;
    var prodi_id = e.target.value;

    if (prodi_id === '')
    {
      $(prodi_id).siblings("input[id=fakultas_id]").val("");
    } else
    {
      $.ajax(
      {
        type: 'GET',
        url: url + '/api/prodies/' + prodi_id,
        dataType: 'JSON'
      })
      .done(function(data)
      {
        $('#prodi_id').empty();
        $.each(data, function(key, prodi)
        {
          $('#prodi_id').append('<option value="' + prodi.id + '">' + prodi.nama_prodi + '</option>');
        });
      })
      .fail(function(error)
      {
        // console.log(error.message);
      });
    }
  });

  /* ---------- Json API - Profile-Change-Password API ---------- */
  $.ajaxSetup(
  {
    headers:
    {
      'X-CSRF-TOKEN': $('meta[name="csrf-token"]').attr('content')
    }
  });

  $('#change-form').submit(function(event)
  {
    event.preventDefault();
    var formAction = $(this).attr('action');

    var postData =
    {
      'old_password': $('input[name=old_password]').val(),
      'password': $('input[name=password]').val(),
      'password_confirmation': $('input[name=password_confirmation]').val()
    };

    $.ajax(
    {
      type: 'PUT',
      url: formAction,
      data: postData,
      dataType: 'JSON'
    })
    .done(function(response)
    {
      $('input[type=password]').val("");
      sOk('Sukses!', response.success, 'success');
      $('#change-info').hide();
    })
    .fail(function(response)
    {
      $('input[type=password]').val("");
      sOk('Kesalahan!', response.responseJSON.error, 'error');
    });
  });

  /* ---------- Json API - User-Change-Password API ---------- */
  $.ajaxSetup(
    {
      headers:
        {
          'X-CSRF-TOKEN': $('meta[name="csrf-token"]').attr('content')
        }
    });

  $('#user-change-form').submit(function(event)
  {
    event.preventDefault();
    var formAction = $(this).attr('action');

    var postData =
      {
        'old_password': $('input[name=old_password]').val(),
        'password': $('input[name=password]').val(),
        'password_confirmation': $('input[name=password_confirmation]').val()
      };

    $.ajax(
      {
        type: 'PUT',
        url: formAction,
        data: postData,
        dataType: 'JSON'
      })
      .done(function(response)
      {
        $('input[type=password]').val("");
        sOk('Sukses!', response.success, 'success');
        $('#change-info').hide();
      })
      .fail(function(response)
      {
        $('input[type=password]').val("");
        sOk('Kesalahan!', response.responseJSON.error, 'error');
      });
  });

  /* ---------- Json API - Forgot-Password API ---------- */
  $.ajaxSetup(
  {
    headers:
    {
      'X-CSRF-TOKEN': $('meta[name="csrf-token"]').attr('content')
    }
  });

  $('#forgot-form').submit(function(event)
  {
    event.preventDefault();
    var formAction = $(this).attr('action');

    var postData =
    {
      'nip': $('input[name=nip]').val()
    };

    $.ajax(
    {
      type: 'POST',
      url: formAction,
      data: postData,
      dataType: 'JSON'
    })
    .done(function(response)
    {
      sOk('Sukses!', response.success, 'success');
      $('#change-info').hide();
    })
    .fail(function(response)
    {
      $('input[type=text]').val("");
      sOk('Kesalahan!', response.responseJSON.error, 'error');
    });
  });

  /* ---------- Json API - Check-Account API ---------- */
  $('#check-form').submit(function(event)
  {
    event.preventDefault();
    var formAction = $(this).attr('action');

    var postData =
    {
      'username': $('input[name=username]').val()
    };

    $.ajax(
    {
      type: 'POST',
      url: formAction,
      data: postData,
      dataType: 'JSON'
    })
    .done(function(response)
    {
      $('input[type=text]').val("");
      $('#change-info').hide();
      sOk('Sukses!', response.success, 'success');
    })
    .fail(function(response)
    {
      $('input[type=text]').val("");
      sOk('Informasi!', response.responseJSON.warning, 'info');
    });
  });

  /* ---------- Json API - Contact Us API ---------- */
  $('#contact-us-form').submit(function(event)
  {
    event.preventDefault();
    var formAction = $(this).attr('action');

    var postData =
    {
      'nama_depan': $('input[name=nama_depan]').val(),
      'nama_belakang': $('input[name=nama_belakang]').val(),
      'kontak_email': $('input[name=kontak_email]').val(),
      'pesan': $('textarea[name=pesan]').val()
    };

    $.ajax(
    {
      type: 'POST',
      url: formAction,
      data: postData,
      dataType: 'JSON'
    })
    .done(function(response)
    {
      $('input[type=text]').val("");
      $('input[type=email]').val("");
      $('#pesan').val("");
      sOk('Sukses!', response.success, 'success');
    })
    .fail(function(response)
    {
      sOk('Informasi!', response.responseJSON.warning, 'info');
    });
  });

  /* ---------- Json API - Imb Pemohon API ---------- */
  $('#imb-pemohon-form').submit(function(event)
  {
    event.preventDefault();
    var formAction = $(this).attr('action');

    var postData =
    {
      'imb_pemohon_id': $('select[name=imb_pemohon_id]').val(),
      'jalan': $('textarea[name=jalan]').val(),
      'kelurahan': $('select[name=kelurahan]').val(),
      'kecamatan': $('select[name=kecamatan]').val(),
      'status_tanah': $('select[name=status_tanah]').val(),
      'advis_planning': $('input[name=advis_planning]').val(),
      'peruntukkan': $('select[name=peruntukkan]').val(),
      'jenis_bangunan': $('select[name=jenis_bangunan]').val(),
      'jenis_lain_lain': $('input[name=jenis_lain_lain]').val()
    };

    $.ajax(
    {
      type: 'POST',
      url: formAction,
      data: postData,
      dataType: 'JSON'
    })
    .done(function(response)
    {
      $('input[type=text]').val("");
      sOk('Sukses!', response.success, 'success');
    })
    .fail(function(response)
    {
      sOk('Informasi!', response.responseJSON.warning, 'info');
    });
  });

});

